<template>
    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; height: 100%; max-width: 550px">
            <v-row>
                <v-spacer></v-spacer>
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{yourLanguagesText}}
                </v-card-title>
                <v-spacer></v-spacer>

                <v-col cols="12">
                    <v-card>
                        <!--<v-progress-linear
                            value="50"
                            height="10"
                            color="primary"
                            reactive
                            dark
                    ></v-progress-linear>-->
                        <div align="center" style="padding-top: 30px">
                            <img style="width: 60px" src="/LogoWeBabble.png"/>
                        </div>

                        <v-container class="pa-3" style="padding-top: 10px!important;">
                            <YourLanguagesSelector
                                    @dataValidityUpdate="isDataValid($event)">
                            </YourLanguagesSelector>

                            <v-btn block color="primary" @click="setUserLanguages" :disabled="buttonIsDisabled">
                                {{continueText}}
                            </v-btn>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>
            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
    import YourLanguagesSelector from "../AuxInputComponents/YourLanguagesSelector";
    import {mapActions} from "vuex";

    export default {
        name: "FillYourLanguages",
        data() {
            return {
                buttonIsDisabled: true,
                knownLanguages: null,
                languagesToLearn: null,
                yourLanguagesText: null,
                continueText: null,
                snackbarText: null,
                snackbar: false,
                closeText: null
            }
        },
        methods: {
            ...mapActions('user', ['setLanguages']),
            isDataValid: function (data) {
                if (data.isValid) {
                    this.languagesToLearn = data.languagesToLearn;
                    this.knownLanguages = data.knownLanguages;
                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            setUserLanguages() {
                //Set languages to user profile
                let resultSetLanguages = this.setLanguages(
                    {
                        knownLanguages: this.knownLanguages,
                        languagesToLearn: this.languagesToLearn
                    });

                resultSetLanguages.then(() => {
                    this.$router.replace('set-your-profile-image');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            }
},
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.yourLanguagesText = this.$i18n.t('messages.TEXT_YOUR_LANGUAGES');
            this.continueText = this.$i18n.t('messages.TEXT_TO_CONTINUE');
        },
        components: {
            YourLanguagesSelector
        }
    }
</script>

<style scoped>
</style>